import React from 'react';
import './popUp.css'

const PopUp = ({ message, setShowPopUp }) => {
  return <div >
    <div>
      <p className='popUp-p'>{message}</p>
      <button onClick={() => setShowPopUp(false)} className="popUp-button font-face-bb" style={{ fontSize: '24px'}}>Close</button>
    </div>
  </div>
}

export default PopUp