import React, { useEffect } from 'react'
import styled from 'styled-components'
import Confetti from 'react-confetti'
import { useWindowSize } from 'react-use'

const Stylesheet = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: #0f0069;
  left: 0;
  z-index: 2;

  p {
    font-size: 12px;
    text-align: center;
    color: rgb(227, 26, 76);
    text-transform: uppercase;
  }

  hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(
      to right,
      rgba(210, 0, 60, 0),
      rgba(210, 0, 60, 0.75),
      rgba(210, 0, 60, 0)
    );
    margin: 2rem 0;
  }
`

const PlayerName = styled.h1`
  color: #fff;
  font-size: 38px;
  text-align: center;
`

const WinnerWrapper = styled.div`
  margin-top: 30vh;
`

const HighestVotedPlayerScreen = ({
  playerName,
  message,
  onClick,
  removeScreenHighestVoted,
  isDraw
}) => {
  const { width, height } = useWindowSize()

  useEffect(() => {
    setTimeout(() => {
      removeScreenHighestVoted()
    }, 3000)
  }, [])

  return (
    <Stylesheet onClick={onClick}>
      <WinnerWrapper>
        {message ? <p>{message}</p> : <p>The group said</p>}
        <hr style={{ marginTop: '.5rem' }} />
        <PlayerName className='font-face-bb'>{playerName}</PlayerName>

        {isDraw === true ? (
          <>
            <hr style={{ marginTop: '.5rem' }} />
            <p>We Have a Draw!!!</p>
          </>
        ) : null}
      </WinnerWrapper>
      <Confetti
        width={width}
        height={height}
        colors={['#D2033B', '#FBD50E']}
        gravity={0.3}
        drawShape={ctx => {
          ctx.beginPath()
          ctx.fillRect(0, 0, 20, 30)
          ctx.stroke()
          ctx.closePath()
        }}
        numberOfPieces={50}
      />
    </Stylesheet>
  )
}

export default HighestVotedPlayerScreen
