import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import can from '../../images/can.png'
import './startPage.css'

const StartPage = () => {
  useEffect(() => {
    localStorage.removeItem('playername')
    localStorage.removeItem('myusername')
    localStorage.removeItem('lastQuestion')
    localStorage.removeItem('lastquestionCounter')
  }, [])

  return (
    <div className='main-container'>
      <div className='container'>
        <div id='instruction'>
          <img src={can} id="redbullCan" alt='redbull can' />
          <p className='font-face-bb'>
            Show us how well you know your friends and earn a{' '}
            <span>Red Bull on us.</span>
          </p>
        </div>
        <Link className='button' id='create' to='/create-game-form'>
          <p className='font-face-bb' style={{ fontSize: '24px', margin: "14px" }}>Create a game</p>
        </Link>
        <div id='or-container'>
          <hr />
          <span className='font-face-bm'>OR</span>
        </div>
        <Link className='button' id='join' to='/join-game'>
          <p className='font-face-bb' style={{ fontSize: '24px', margin: "14px" }}>Join a game</p>
        </Link>
        <div><div id='warn' className='font-face-bm'>*Min. 3 players required</div></div>
        {/* <p style={{ color: '#fff', marginTop: '10px', fontSize: '14px', opacity: '50%', textAlign: 'center' }}>Do not close your phone screen as you will be disconnected from the game</p> */}
      </div>
    </div>
  )
}

export default StartPage
