import React, { useEffect, useState } from 'react'
import { Redirect } from 'react-router'
import styled from 'styled-components'
import PopUp from '../popUp/popUp'
import socket from '../../socket'

const PlayersList = styled.ul`
  width: 100%;
  min-width:300px;
  height: 30vh;
  max-height:275px;
  padding: 0;
  list-style-type: none;
  overflow-y: auto;
  position: relative;  
`

const JoinedPlayer = styled.li`
  color: #0f0069;
  background: #ffcc00;
  padding: 1rem;
  margin-bottom: 5px;
  display: flex;
  justify-content: space-between;
  font-family: "BullText-Bold";
  border-radius: 3px;
`

const Creator = styled.h1`
  color: #fff;
  text-align: center;
  font-size: 38px;
  overflow: hidden; 
`

const ListWrapper = styled.div`
  position: relative;
`

const AwaitMessage = styled.p`
  color: #fff;
  text-align: center;
  font-size: 20px;
  position: absolute;
  bottom: -15%;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
`

const Lobby = ({
  players,
  gameCreator,
  wrongCode,
  setWrongCode,
  limitExceeded,
  setLimitExceeded
}) => {
  const [hasGameStarted, setGameStarted] = useState(false)
  const [redirect, setRedirect] = useState(false)

  useEffect(() => {
    socket.on('gameStarted', gameStarted => setGameStarted(gameStarted))
  }, [])

  const handleWrongCode = () => {
    setRedirect(true)
    setWrongCode(false)
  }

  const handleLimitExceeded = () => {
    setRedirect(true)
    setLimitExceeded(false)
  }

  return (
    <div className='main-container'>
      <div className='container'>
        {hasGameStarted ? <Redirect to='/game' /> : ''}
        {wrongCode ? (
          <PopUp setShowPopUp={handleWrongCode} message='Invalid code' />
        ) : (
          ''
        )}

        {limitExceeded ? (
          <PopUp setShowPopUp={handleLimitExceeded} message='Room is full!' />
        ) : (
          ''
        )}

        {redirect ? <Redirect to='/join-game' /> : ''}
        {!wrongCode ? <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>

          <Creator className='font-face-bb'>Team {gameCreator.teamname}'s game</Creator>
          <hr />
          <ListWrapper>
            <PlayersList >
              {players
                ? players.map((player, index) =>
                  player.socketId !== gameCreator.socketId ? (
                    <JoinedPlayer key={index}>{player.username}</JoinedPlayer>
                  ) : (
                    ''
                  )
                )
                : ''}
            </PlayersList>
          </ListWrapper>
          <AwaitMessage>Awaiting game creator to start the game...</AwaitMessage>
        </div> : null}
      </div>

    </div>
  )
}

export default Lobby
