
import io from 'socket.io-client'
//const SOCKET_URI = "https://who-is-new.herokuapp.com"
const SOCKET_URI = "http://localhost:4000"
// const SOCKET_URI = "https://web07014.microsites07.redbull.com"
const socket = io(SOCKET_URI, {
  transports: ["polling", "websocket"]
});

export function startSocket() {
  if (!socket.connected) {
    socket.connect();
    socket.emit('rejoin', { roomCode: localStorage.getItem('roomCode'), username: localStorage.getItem('myusername') })
    // socket.emit('backuplostdata',{gameCode:localStorage.getItem('gameCode'),userName:localStorage.getItem('playername')})
  } else {
    console.log('socket connection status: ', socket.connected);
  }
}

export function stopSocket() {
  if (socket.connected) {
    socket.disconnect();
    console.log('socket connected: ', socket.connected);
  } else {
    console.log('no active socket connection found');
  }
}

export default socket;