import React from 'react'
import redbull from '../../images/redbull.png'
import './winnerScreen.css'

const WinnerScreen = (props) => {

  return (
    <div className='main-container'>
      <div className='container'>
        <img id="imgbig" src={redbull} alt='redbull' />
        <div className='WinnerWrapper'>
          <div className='WinnerInner'>
            <p className='Winner font-face-bb'>{props.winner} WINS!</p>
            <p className='WinnerP'>You are a born winner!</p>
            <a
              style={{ background: '#d2003c !important' }}
              onClick={() => {
                window.gtag('event', 'Restart Game', {
                  event_category: 'Restart Game',
                  event_label: 'Restart Game'
                })
                window.dataLayer = window.dataLayer || []
                window?.dataLayer.push({
                  event: 'gaEvent',
                  eventCategory: 'Restart Game',
                  eventAction: 'Restart Game',
                  eventLabel: 'Restart Game'
                })

                localStorage.removeItem('playername')
                window.location.assign('/')
              }}
              className='button font-face-bb'
              id='get-redbull'
              href='https://www.redbull.com/us-en/projects/experience#/offer/onp-mini-games-test'
            >
              Get a Red Bull on us
            </a>
            <div className='button font-face-bb' id='seeagain' onClick={() => props.showScore()}>
              See scores again
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default WinnerScreen
