import styled from 'styled-components'
import HighestVotedPlayerScreen from '../highestVotedPlayerScreen/highestVotedPlayerScreen'
import TimeIsUpContain from '../timIsUp/timeIsUp'
import ScoreStats from '../scoreStats/scoreStats'
import { Redirect, useHistory } from 'react-router-dom'
import ProgressBar from '../progressBar/progressBar'
import ProgressBarDecider from '../progressBarDecider/progressBarDecider'

