const Header = styled.div`
  text-align: center;
  margin-top: 0rem;
  margin-bottom: 0rem;

  img {
    width: 16rem;
  }

  h1 {
    color: #fff;
    font-size: 32px;
    margin-top: 10px;
  }

  p {
    text-transform: uppercase;
    color: #d2003c;
    font-size: 12px;
  }
`

const Game = ({ playersInGame, socket, roomCode, runMeAgain, reset }) => {

  const [question, setQuestion] = useState('')
  const [questionCounter, setQuestionCounter] = useState(0)
  const [isAnyDraw, setIsAnyDraw] = useState(false)

  const [highestVotedPlayer, setHighestVotedPlayer] = useState('')
  const [timeIsUp, setTimeIsUp] = useState(false);
  const [playersStatistics, setPlayersStatistics] = useState([])
  //tied and untied users and my name

  const [tiedPlayers, setTiedPlayers] = useState([])
  const [nonTiedPlayers, setNonTiedPlayers] = useState([])
  const [whoami, setWhoAmI] = useState('')

  //tied and untied users and my name :END HERE

  const [winner, setWinner] = useState('')
  const [hasVoted, setHasVoted] = useState(false)
  const [hasRefreshed, setHasRefreshed] = useState(false)
  const [locationKeys, setLocationKeys] = useState([])
  const history = useHistory()
  const [questionCount, setQuestionCount] = useState(7)
  const [drawHappended, setDrawHappended] = useState(false)

  useEffect(() => {
    setTimeout(() => {
      let user = playersInGame?.find(x => x.socketId === socket.id)
      // console.log(user?.username)
      if (user?.username) {
        setWhoAmI(user?.username)
      }
    }, 5000)
    if (!socket) {
      setHasRefreshed(true)
    }

    socket.emit('firstQuestion', roomCode)
    socket.on('sendFirstQuestion', question => {
      setQuestion(question);
      localStorage.removeItem('lastQuestion')
      localStorage.setItem('lastQuestion', question);
    })
    // socket.on('YourLastData',(data)=>{
    //   alert(JSON.stringify(data))
    //   if(data.username==localStorage.getItem('myusername')){
    //     alert('seting up last question')
    //     setQuestion(data.lastData.question)
    //     setQuestionCounter(data.lastData.questionCounter)
    //   }
    // })

    // let counter = 0

    // socket.on('questionCount', count => setQuestionCount(count))

    socket.on('nextQuestionTime', data => {
      if (question !== null) {
        setQuestion(data.question)
        setQuestionCounter(data.counter)
        if (data.counter < 7) {
          console.log("nextquestiontime")
          setTimeIsUp(true);
        } 
        localStorage.removeItem('lastQuestion')
        localStorage.setItem('lastQuestion', data.question)

        localStorage.removeItem('lastquestionCounter')
        localStorage.setItem('lastquestionCounter', data.counter)
      }
    })

    socket.on('backupLastData', (data) => {
      //  socket.emit('lastData',{question:question,questionCounter:questionCounter})
      if (data.username === localStorage.getItem('myusername')) {
        setQuestion(localStorage.getItem('lastQuestion'))
        setQuestionCounter(parseInt(localStorage.getItem('lastquestionCounter')))
      }
    })
  }, [])

  useEffect(() => {
    // socket.on('getPlayers', data => console.log(data))
    return history.listen(location => {
      if (history.action === 'PUSH') {
        setLocationKeys([location.key])
      }

      if (history.action === 'POP') {
        if (locationKeys[1] === location.key) {
          setLocationKeys(([_, ...keys]) => keys)

          // Handle forward event
        } else {
          setLocationKeys(keys => [location.key, ...keys])

          // Handle back event
          window.confirm('Are you sure you want to end the game?')
        }
      }
    })
  }, [locationKeys])

  const removeScreenHighestVoted = () => {
    setHighestVotedPlayer('')
  }

  const removeTimeIsUp = () => {
    setTimeIsUp(false);
    setHasVoted(false);
    const joinedPlayers = Array.from(
      document.getElementsByClassName('joinedPlayers')
    )

    joinedPlayers.forEach(player => player.classList.remove('selected'))
  }

  const removeStatsScreen = () => {
    setHighestVotedPlayer('')

    // socket.on('highestVotedReached', (data) => setWinner(data))

    playersStatistics.forEach(player => {
      if (player.points >= 10) {
        setWinner(player.username)
      }
    })
  }

  const handleNextQuestion = data => {
    // socket.on('getPlayers', data => setPlayersStatistics(data))
    if (data) {
      setQuestion(data.question)
      setQuestionCounter(data.counter)

      localStorage.removeItem('lastQuestion')
      localStorage.setItem('lastQuestion', data.question)

      localStorage.removeItem('lastquestionCounter')
      localStorage.setItem('lastquestionCounter', data.counter)
      setHasVoted(false)
    }
  }

  const selectAnswer = (event, player) => {
    const joinedPlayers = Array.from(
      document.getElementsByClassName('joinedPlayers')
    )

    joinedPlayers.forEach(player => player.classList.remove('selected'))
      console.log(event.target.classList);
    event.target.classList.add('selected')

    const data = {
      player,
      socketId: socket.id,
      roomCode: localStorage.getItem('roomCode'),
      username: localStorage.getItem('myusername')
    }

    socket.emit('vote', data)
    socket.on('highestVotedPlayer', player => {
      setHighestVotedPlayer(player)
      return
    })

    socket.on('isDraw', data => {
      setIsAnyDraw(data)
    })

    socket.on('results', data => {
      console.log(data.length, "results")
      if (isAnyDraw === true && data.length >= 6) {
        console.log("is Any Draw True")
        let arr = data
        let b = {}
        arr.forEach(el => {
          b[el.username] = (b[el.username] || 0) + 1
        })

        let finalOutput = []
        const keys = Object.keys(b)

        keys.forEach((key, index) => {
          let obj = {
            username: key,
            points: b[key]
          }
          finalOutput.push(obj)
        })

        let maxPoints = 0
        for (let i = 0; i < finalOutput.length; i++) {
          if (finalOutput[i].points > maxPoints) maxPoints = finalOutput[i].points
        }

        var valueArr = finalOutput.map(function (item) {
          return item.points
        })

        let duplicatedPoints = 0
        var isDuplicate = valueArr.some(function (item, idx) {
          if (valueArr.indexOf(item) !== idx) duplicatedPoints = item
          return valueArr.indexOf(item) !== idx
        })

        console.log("duplicated points: ", duplicatedPoints)
        // console.log('HERE in if')
        if (isDuplicate === false || duplicatedPoints !== maxPoints) {
          setPlayersStatistics(finalOutput)
        } else {
          // console.log('HERE in if-else')
          console.log(finalOutput[0].points)
          //call function here
          tiedNonTied(finalOutput)
        }
      } else if (questionCounter >= 6) {
        console.log("is Any Draw False")
        let arr = data
        let b = {}
        arr.forEach(el => {
          b[el.username] = (b[el.username] || 0) + 1
        })

        let finalOutput = []
        const keys = Object.keys(b)

        keys.forEach((key, index) => {
          let obj = {
            username: key,
            points: b[key]
          }
          finalOutput.push(obj)
        })
        let maxPoints = 0
        for (let i = 0; i < finalOutput.length; i++) {
          if (finalOutput[i].points > maxPoints) maxPoints = finalOutput[i].points
        }
        var valueArr = finalOutput.map(function (item) {
          return item.points
        })
        let duplicatedPoints = 0;
        var isDuplicate = valueArr.some(function (item, idx) {
          if (valueArr.indexOf(item) !== idx) duplicatedPoints = item
          return valueArr.indexOf(item) !== idx
        })

        console.log("isDuplicated Points:", duplicatedPoints)

        if (isDuplicate === false || duplicatedPoints !== maxPoints) {
          setPlayersStatistics(finalOutput)
        } else {
          //call function here
          tiedNonTied(finalOutput)
        }
      }

      return
    })
    socket.on('nextQuestion', data => {
      handleNextQuestion(data)

      event.target.classList.remove('selected')
    })

    setHasVoted(true)
  }

  const tiedNonTied = finalOutput => {
    // Count the highested voted players after finish 7 questions
    console.log(finalOutput.length)
    setDrawHappended(true)
    let topVotedPlayers = []
    let maxPoints = 0
    for (let i = 0; i < finalOutput.length; i++) {
      if (finalOutput[i].points > maxPoints) maxPoints = finalOutput[i].points
    }
    for (let i = 0; i < finalOutput.length; i++) {
      if (finalOutput[i].points === maxPoints) topVotedPlayers.push(finalOutput[i])
    }
    // Tied user 
    let tiedUserArr_Initial = []
    // for (let index = 0; index < finalOutput.length; index++) {
    //     const element = finalOutput[index]
    //     if (element.points == finalOutput[0].points) {
    //       tiedUserArr_Initial.push(element.username)
    //     }
    //   }
    // }
    for(let index = 0; index < finalOutput.length; index ++) {
      if(finalOutput[index].points === maxPoints) tiedUserArr_Initial.push(finalOutput[index].username)
    }
    
    if (tiedUserArr_Initial.length < 2) tiedUserArr_Initial = []
    console.log('tiedUserArr_Initial', tiedUserArr_Initial)

    let tiedUserArr_final = []
    for (let index = 0; index < tiedUserArr_Initial.length; index++) {
      const element = tiedUserArr_Initial[index]
      let response = playersInGame.filter(obj => obj.username === element)[0]
      console.log('response', response)
      tiedUserArr_final.push(response)
    }
    console.log('tiedUserArr_final', tiedUserArr_final)

    setTiedPlayers(tiedUserArr_final)
    //non tied folks logic

    let toRemove = tiedUserArr_final

    let non_tiedUserArr_final = [...playersInGame]

    //purify it now
    for (var i = non_tiedUserArr_final.length - 1; i >= 0; i--) {
      for (var j = 0; j < toRemove.length; j++) {
        if (
          non_tiedUserArr_final[i] &&
          non_tiedUserArr_final[i]?.username === toRemove[j]?.username
        ) {
          non_tiedUserArr_final.splice(i, 1)
        }
      }
    }
    console.log('non_tiedUserArr_final', non_tiedUserArr_final)
    setNonTiedPlayers(non_tiedUserArr_final)
    return
  }

  const beforeAnyDraw = () => {
    return (
      <>
        <QuestionWrapper>
          <p>
            {questionCounter >= 7
              ? 'Deciding Quesition!!!'
              : `Question # ${questionCounter + 1} / ${questionCount}`}
          </p>
          <hr style={{ marginTop: '5px', marginBottom: '1rem' }} />
          <p style={{ color: '#ffcc00', fontSize: '12px' }}>SELECT WHO IS MOST LIKELY TO DO THE BELOW</p>
          {/* <button onClick={() => runMeAgain(true)}>Run it again</button> */}
          <QuestionText
            className='font-face-bm'
            style={{
              fontSize: question?.split(/\S+/).length - 1 > 8 ? '1.05rem' : '1.05rem'
            }}
          >
            {question}
          </QuestionText>
          {/* <hr style={{ marginTop: '5px' }} /> */}
        </QuestionWrapper>

        <PlayersList>
          {playersInGame
            ? playersInGame?.map((player, index) =>
              (player.socketId !== socket.id) && (player.username !== localStorage.getItem('myusername')) ? (
                <JoinedPlayer
                  className='joinedPlayers noselect'
                  onClick={event => selectAnswer(event, player)}
                  key={index}
                >
                  {player.username}
                </JoinedPlayer>
              ) : (
                ''
              )
            )
            : ''}
        </PlayersList>

        <ProgressBar roomCode={roomCode} socket={socket} />

        <span>
          <p
          className='font-face-bb'
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              color: '#ffcc00',
              marginBottom: '8rem'
            }}
          >
            Waiting for other players to vote
          </p>
        </span>
        {highestVotedPlayer !== '' ? (
          <HighestVotedPlayerScreen
            removeScreenHighestVoted={removeScreenHighestVoted}
            playerName={highestVotedPlayer.username}
            message={highestVotedPlayer.message}
            isDraw={drawHappended}
          />
        ) : (
          ''
        )}

        {playersStatistics.length >= 1 ? (
          <ScoreStats
            removeStatsScreen={removeStatsScreen}
            players={playersStatistics}
          />
        ) : (
          ''
        )}

        {/* {winner !== '' ? <WinnerScreen winner={winner} reset={reset} /> : ''} */}
      </>
    )
  }

  const afterDrawDisplayDecision = () => {
    return (
      <>
        {nonTiedPlayers.findIndex(item => item.username === localStorage.getItem('myusername')) ===
          -1 ? (
          <p
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              color: 'white',
              fontSize: '28px',
              fontFamily: "BullText-Bold",
              marginTop: '-30px',
              textAlign: 'center'
            }}
          >
            {playersStatistics.length <= 1 ? 'Please wait while the winner is decided!' : ''}
          </p>
        ) :
          <>

            {!hasVoted ? <QuestionWrapper>
              <p>
                {questionCounter >= 7
                  ? 'Deciding Quesition!!!'
                  : `Question # ${questionCounter + 1} / ${questionCount}`}
              </p>
              <hr style={{ marginTop: '5px', marginBottom: '1rem' }} />
              <p>SELECT WHO IS MOST LIKELY TO DO THE BELOW</p>
              {/* <button onClick={() => runMeAgain(true)}>Run it again</button> */}
              <QuestionText
                className='font-face-bm'
                style={{
                  fontSize:
                    question?.split(/\S+/).length - 1 > 8 ? '18px' : '33px'
                }}
              >
                {question}
              </QuestionText>
              <hr style={{ marginTop: '5px' }} />
            </QuestionWrapper> : null}

            {!hasVoted ? <PlayersList>
              {tiedPlayers.length === 0
                ? nonTiedPlayers?.map((player, index) => (
                  <JoinedPlayer
                    className='joinedPlayers noselect'
                    onClick={event => selectAnswer(event, player)}
                    key={index}
                  >
                    {player.username}
                  </JoinedPlayer>
                ))
                : ''}
            </PlayersList> : null}
            {!hasVoted ? <PlayersList>
              {tiedPlayers
                ? tiedPlayers?.map((player, index) => (
                  <JoinedPlayer
                    className='joinedPlayers noselect'
                    onClick={event => selectAnswer(event, player)}
                    key={index}
                  >
                    {player.username}
                  </JoinedPlayer>
                ))
                : ''}
            </PlayersList> : null}

            {!hasVoted ? <ProgressBarDecider roomCode={roomCode} socket={socket} /> : null}

            {highestVotedPlayer !== '' && !hasVoted ? (
              <HighestVotedPlayerScreen
                removeScreenHighestVoted={removeScreenHighestVoted}
                playerName={highestVotedPlayer.username}
                message={highestVotedPlayer.message}
                isDraw={drawHappended}
              />
            ) : (
              ''
            )}
            {playersStatistics.length >= 1 && !hasVoted ? (
              <ScoreStats
                removeStatsScreen={removeStatsScreen}
                players={playersStatistics}
              />
            ) : (
              ''
            )}
            {/* {winner !== '' ? <WinnerScreen winner={winner} reset={reset} /> : ''} */}
          </>
        }
      </>
    )
  }
  // use highestVotedPlayer.username in an array and check the highes person at the end.
  return (
    <Stylesheet className='fade-in'>
      {timeIsUp === true ? (
        <TimeIsUpContain
          removeTimeIsUp={removeTimeIsUp}
          playerName={'username'}
          message={'message'}
          isDraw={true}
        />
      ) : (
        ''
      )}
      {highestVotedPlayer !== '' ? (
        <HighestVotedPlayerScreen
          removeScreenHighestVoted={removeScreenHighestVoted}
          playerName={highestVotedPlayer.username}
          message={highestVotedPlayer.message}
          isDraw={drawHappended}
        />
      ) : (
        ''
      )}

      {playersStatistics.length >= 1 ? (
        <ScoreStats
          removeStatsScreen={removeStatsScreen}
          players={playersStatistics}
        />
      ) : (
        ''
      )}

      {/* {winner !== '' ? <WinnerScreen winner={winner} reset={reset} /> : ''} */}

      {hasRefreshed ? <Redirect to='/' /> : ''}
      <Container>
        {/* <Header>

        </Header> */}
        {questionCounter >= 7 ? afterDrawDisplayDecision() : beforeAnyDraw()}
      </Container>
    </Stylesheet>
  )
}

export default Game
