import React from 'react'
import styled from 'styled-components'
import star from '../../images/star.png'
import './progress.css'

const Progress = ({ value }) => {
  const Stylesheet = styled.div`
    position: relative;
    img {
      position: absolute;
      animation: mymove 5s forwards !important;
      animation-iteration-count: 1 !important;
      animation-fill-mode: forwards !important;

      width: 25px;
      z-index: 1;
      bottom: -11px;
      transform: rotate(90deg);
    }
    @keyframes mymove {
      from {
        left: 0%;
      }
      to {
        left: ${value}+ '%';
      }
    }
  `

  return (
    <Stylesheet>
      <hr
        style={{
          background: `linear-gradient(90deg, rgba(210,3,59,1) 0%, rgba(222,186,0,1) ${value}%, rgba(33,44,72,1) 0%)`
        }}
        id='progress'
      />
      <img
        style={{
          left: value - 1 + '%'
        }}
        src={star}
        alt='yellow star'
      />
    </Stylesheet>
  )
}

export default Progress
