import React, { useState } from 'react'
import './Footer.css';

export default function Footer({showFooter,setShowFooter}) {
    return (
        showFooter ? <>
        <button className='header__menuIcon' style={{ zIndex: 1500, position: 'fixed', bottom: '8px', right: '10px', background:"#001e3c" }} onClick={() => { setShowFooter(false) }}> <i class="fas fa-times cross-icon"></i></button>
        <div id="footer">
            <rb-footer theme="dark" locale="int-en"></rb-footer>
        </div></>
            : 
        <button className='header__menuIcon' style={{ zIndex: 5, position: 'fixed', bottom: '8px', right: '10px' }} onClick={() => { setShowFooter(true) }} >
            <div style={{ display: 'flex' }}> <i className="fas fa-ellipsis-v"></i>
                <i className="fas fa-ellipsis-v"></i>
                <i className="fas fa-ellipsis-v"></i>
            </div>
        </button>
    )
}
