import React, { useState, useEffect } from 'react'
import Progress from '../progress/progress'
import WinnerScreen from '../winnerScreen/winnerScreen'
import LooserScreen from '../looserScreen/looserScreen'
import './scoreStats.css'

//this is what we should show
const ScoreStats = ({ onClick, reset, players }) => {
  const [winner, setWinner] = useState('')
  const [myName, setMyName] = useState('')
  const [showStats, setShowStats] = useState(true)
  const [shouldDisplay, setShouldDisplay] = useState(false)
  const playersSorted = players.sort((a, b) => (a.points < b.points ? 1 : -1))

  setTimeout(() => {
    let my_name = localStorage.getItem('myusername')
    setMyName(my_name)
    setWinner(players[0].username)
    setShouldDisplay(true)
  }, 3000)

  useEffect(() => {
    setTimeout(() => {
      setShowStats(false)
    }, 10000);
  }, [])
  const decideToShow = () => {
    return (
      <div>
        {winner === myName ? (
          <WinnerScreen showScore={() => { setShowStats(true) }} winner={winner} reset={reset} />
        ) : (
          <LooserScreen showScore={() => { setShowStats(true) }} winner={winner} reset={reset} />
        )}
      </div>
    )
  }
  return (
    <div onClick={onClick}>
      {showStats ? <div className='container'>
        <p className='Finalscore'>Final scores!</p>

        <div className='PlayerScoresWrapper'>
          {playersSorted.map((player, index) => (
            <div key={index}>
              <div className='PlayerInfo'>
                <div className='PlayerName'>{player.username}</div>
                <p className='points span-scoreStats'>
                  {player.points > 10 ? 10 : player.points} <span className='span-scoreStats'> Votes</span>
                </p>
              </div>
              <Progress value={player.points >= 4 ? 60 : player.points * 14.28} />
            </div>
          ))}
        </div>

        <div
            id='seeWinnerBtn'
            className='button font-face-bb'
            onClick={() => setShowStats(false)}
          >
            See winner
          </div>
      </div> : shouldDisplay === true ? decideToShow() : ''}
    </div>
  )
}

export default ScoreStats
