import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import './joinGame.css'

const JoinGame = ({ setJoinRoomCode, joinGame, setUserName }) => {
  const [userNameVerify, setuserNameVerify] = useState('')
  const isDiabled = () => {
    if (userNameVerify !== '') {
      return false
    }
    return true
  }

  const set_UserName = event => {
    event.preventDefault()
    setUserName(event.target.value)
    setuserNameVerify(event.target.value)
  }

  return (
    <div className='main-container'>
      <div className='container' >

        <h2>Join a game</h2>

        <p>Enter game code</p>

        <input
          onChange={event => {
            setJoinRoomCode(event.target.value)
          }}
          type='text'
          className='input-box'
          placeholder='Enter Game Code'
        />

        <p>Your nickname</p>

        <input
          onChange={event => {
            set_UserName(event)
          }}
          type='text'
          className='input-box'
          placeholder='Enter Name'
        />
        <br></br>
        <Link
          style={{ pointerEvents: isDiabled() === true ? 'none' : 'all', cursor: 'pointer' }}
          id='join'
          className='join-create-game'
          onClick={joinGame}
          to='/lobby'
        >
          <p className='font-face-bb' style={{ fontSize: '26px', margin: "10px"}}>Join</p>
        </Link>

        <br></br>
        <Link
          onClick={() => {
            window.gtag('event', 'Back Button', {
              event_category: 'Back Button',
              event_label: 'Back Button'
            })
            window.dataLayer = window.dataLayer || []
            window?.dataLayer.push({
              event: 'gaEvent',
              eventCategory: 'Back Button',
              eventAction: 'Back Button',
              eventLabel: 'Back Button'
            })
          }}
          id='back'
          className='back-create-game'
          to='/'
        >
          <p className='font-face-bb' style={{ fontSize: "26px", margin: "10px"}}>Back</p>
        </Link>

      </div>
    </div>
  )
}

export default JoinGame
