import React, { useEffect, useState } from 'react'

const ProgressBarDecider = ({ roomCode, socket }) => {
  const [completed, setCompleted] = useState(100)
  const [seconds, setSeconds] = useState(20)

  useEffect(() => {
    setInterval(() => {
      if (socket) {
        socket.emit('startTimer', { roomCode })
        socket.on('tick', data => {
          setCompleted(data.completed)
          setSeconds(data.seconds)
        })
      }
    }, 1000)
  }, [])

  const containerStyles = {
    height: 20,
    width: '100%',
    backgroundColor: '#e0e0de',
    borderRadius: 50,
    marginBottom: -50,
    position: 'absolute',
    bottom: '2vh'
  }

  const fillerStyles = {
    height: '100%',
    width: `${completed}%`,
    backgroundColor: '#D2033B',
    borderRadius: 'inherit',
    textAlign: 'right'
  }

  const labelStyles = {
    padding: 5,
    color: 'white',
    fontWeight: 'bold'
  }

  function secondsToHms(d) {
    d = Number(d)
    // let h = Math.floor(d / 3600)
    let m = Math.floor((d % 3600) / 60)
    let s = Math.floor((d % 3600) % 60)

    let mDisplay = m > 0 ? m + (m === 1 ? ':' : '') : ''
    let sDisplay = s > 0 ? s + (s === 1 ? '' : '') : ''
    return mDisplay + sDisplay
  }

  return (
    <div>
      <div style={containerStyles}>
        <div style={fillerStyles}>
          <span style={labelStyles}>{secondsToHms(seconds)}</span>
        </div>
      </div>
    </div>
  )
}

export default ProgressBarDecider
