const CreateRoom = ({
  roomCode,
  gameCreator,
  joinedPlayers,
  setJoinedPlayers,
  removePlayer,
  startGame,
}) => {
  const [showPopUp, setShowPopUp] = useState(false);
  const [redirect, setRedirect] = useState("/create-game");
  const [locationKeys, setLocationKeys] = useState([]);
  const history = useHistory();

  useEffect(() => {
    return history.listen((location) => {
      if (history.action === "PUSH") {
        setLocationKeys([location.key]);
      }

      if (history.action === "POP") {
        if (locationKeys[1] === location.key) {
          setLocationKeys(([_, ...keys]) => keys);

          // Handle forward event
        } else {
          setLocationKeys((keys) => [location.key, ...keys]);

          // Handle back event
          setJoinedPlayers([]);
        }
      }
    });
  }, [locationKeys]);

  //atleast 3 players logic
  const start = () => {
    if (joinedPlayers.length >= 3) {
      window.gtag("event", "Start game", {
        event_category: "Start game",
        event_label: "Start game",
      });

      window.dataLayer = window.dataLayer || [];
      window?.dataLayer.push({
        event: "gaEvent",
        eventCategory: "Start game",
        eventAction: "Start game",
        eventLabel: "Start game",
      });

      startGame();
      setRedirect("/game");
    } else {
      setShowPopUp(true);
      setRedirect("/create-game");
    }
  };

  return (
    <div className="main-container">
      <div className="container">
        {showPopUp ? (
          <PopUp
            setShowPopUp={setShowPopUp}
            message="This game requires a minimum of 3 users to play."
          />
        ) : null}

        {redirect === "/game" ? <Redirect to="/game" /> : null}
        {showPopUp ? null : (
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <div className="creator font-face-bb">Team {gameCreator.teamname}'s Game</div>
            <div>
              <p id="gameCodeHead">Your game code</p>
              <hr style={{ margin: "10px 0" }} />
              <div className="font-face-bm">
                Share this code with your friends so they can join your game.
              </div>
              <div className="RoomCodeText font-face-bb">{roomCode}</div>
            </div>
            <hr />
            <div id="creatorLobby">
              {joinedPlayers
                ? joinedPlayers.map((player, index) =>
                  player.socketId !== gameCreator.socketId ? (
                    <div key={index} className="joinedPlayers">
                      <p className="player font-face-bb">{player.username}</p>
                      <p className="close" style={{ cursor: 'pointer', padding: '5px', color: 'whitesmoke' }} onClick={() => removePlayer(player)}>X</p>
                    </div>
                  ) : null
                )
                : null}
            </div>
          </div>
        )}

        {showPopUp ? null : (
          <Link onClick={start} className="button" id="startGame" to={redirect}>
            <p className='font-face-bb' style={{ fontSize: '24px', margin: '10px'}}>Start game</p>
          </Link>
        )}
      </div>
    </div>
  );
};

export default CreateRoom;
