import React from 'react'
import { Link } from 'react-router-dom'
import './createNewGameForm.css'

const CreateNewGameForm = ({ createNewGame, setTeamName, setCreatorName }) => {
  return (
    <div className='main-container'>
      <div className='container'>
        <h2 className='font-face-bb'>Create a game</h2>

        <p >What’s your team name?</p>

        <input
          placeholder='Your team name'
          onChange={event => setTeamName(event.target.value)}
          type='text'
          className='input-box'
        />
        <div id="blank"></div>
        <p> What's your nickname?</p>

        <input
          placeholder='Your player name'
          onChange={event => setCreatorName(event.target.value)}
          type='text'
          className='input-box'
        />
        <br></br>
        <Link
          id='join'
          className='button join-create-game'
          onClick={createNewGame}
          to='/create-game'
        >
          <p className='font-face-bb' style={{ fontSize: '24px', margin: "10px" }}>Create game</p>
        </Link>
        <br></br>

        <Link id='back' className='button back-create-game' to='/'>
          <p className='font-face-bb' style={{ fontSize: "24px", margin: "10px"}}>Back</p>
        </Link>

      </div>
    </div>
  )
}

export default CreateNewGameForm
