import React, { useEffect, useState } from 'react'
import './App.css'
import './fonts/BullText-Medium.ttf'
import './fonts/BullText-Bold.ttf'
import './fonts/BullText-Bold.woff'
import './fonts/BullText-Bold.woff2'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import StartPage from './components/startPage/startPage'
import CreateRoom from './components/createRoom/createRoom'
import socket, { startSocket } from './socket'
import JoinGame from './components/joinGame/joinGame'
import Lobby from './components/lobby/lobby'
import Game from './components/game/Game'
import CreateNewGameForm from './components/createNewGameForm/createNewGameForm'
import Header from './components/header/header'
import Footer from './components/footer/Footer'

function App() {
  // const [socket, setSocket] = useState()
  const [roomCode, setRoomCode] = useState()
  const [joinRoomCode, setJoinRoomCode] = useState()
  const [joinedPlayers, setJoinedPlayers] = useState([])
  const [gameCreator, setGameCreator] = useState([{ socketId: '' }])
  const [playersInLobby, setPlayersInLobby] = useState(null)
  const [userName, setUserName] = useState()
  const [creatorName, setCreatorName] = useState()
  const [teamName, setTeamName] = useState()
  const [gameStarted, setGameStarted] = useState(false)
  const [wrongCode, setWrongCode] = useState(false)
  const [limitExceeded, setLimitExceeded] = useState(false)

  const [showNavContent, setShowNavContent] = useState(false);
  const [showFooter, setShowFooter] = useState(false)

  //   window.onbeforeunload = function() {
  //     return "Changes may not be saved!";
  // }

  useEffect(() => {
    startSocket()
  }, [])

  useEffect(() => {
    socket.on('ipBlocked', (data) => {
      if (data.username === localStorage.getItem('myusername')) {
        alert('you are exceeding maximum attempt limit!!');
        window.location.href = "/"
      }
    })
    socket.on('name-duplication', (data) => {
      if (data.username === localStorage.getItem('myusername')) {
        alert(`username "${data.username}" already taken!,please choose another name`);
        window.location.href = "/"
      }
    })
  }, [])

  const createNewGame = () => {
    const data = {
      teamname: teamName,
      username: creatorName,
    }
    
    localStorage.removeItem('playername')
    localStorage.setItem('playername', creatorName)
    localStorage.removeItem('myusername')
    localStorage.setItem('myusername', creatorName)
    socket.emit('newGame', data)

    socket.on('gameCode', data => {
      setRoomCode(data)
      setJoinRoomCode(data)
      localStorage.removeItem('roomCode')
      localStorage.setItem('roomCode', data)
    })

    socket.on('gameCreator', gameCreator => setGameCreator(gameCreator))
    socket.on('getPlayers', players => setJoinedPlayers(players))
  }

  const joinGame = () => {
    window.gtag('event', 'Join Game', {
      event_category: 'Join Game',
      event_label: 'Join Game'
    })
    window.dataLayer = window.dataLayer || []
    window?.dataLayer.push({
      event: 'gaEvent',
      eventCategory: 'Join game',
      eventAction: 'Join game',
      eventLabel: 'Join game'
    })

    let data = {
      roomCode: joinRoomCode,
      username: userName,

    }
    localStorage.removeItem('myusername')
    localStorage.setItem('myusername', userName)
    localStorage.removeItem('roomCode')
    localStorage.setItem('roomCode', joinRoomCode)
    socket.emit('joinGame', data)
    socket.on('getPlayers', players => setPlayersInLobby(players))
    socket.on('unknownCode', data => {
      if (data === 'unknown Code') {
        setWrongCode(true)
      }
    })

    socket.on('limitExceeded', data => {
      if (data === 'limitExceeded') {
        setLimitExceeded(true)
      }
    })

    socket.on('gameCreator', gameCreator => setGameCreator(gameCreator))
  }

  const removePlayer = player => {
    socket.emit('removePlayer', player)
  }

  const startGame = () => {
    socket.emit('gameStart', roomCode)
    socket.on('getPlayers', players => setPlayersInLobby(players))
    socket.on('gameStarted', gameStarted => setGameStarted(gameStarted))
  }

  useEffect(() => {
    socket.on('rejoinThisRoom', (data) => {
      if (data.myData.socketId === socket.id) {
        if (!playersInLobby) {
          setPlayersInLobby(data.players)
          socket.emit('resetMyData', data)
        }
      }
    })
  }, [])

  const reset = () => {
    setJoinedPlayers('')
    setGameCreator('')
    setPlayersInLobby('')
  }

  const runMeAgain = reRun => {
    socket.on('getPlayers', players => console.log(players))
    // console.log(socket.on('getPlayers', players => console.log(players)))
    return
    // if (reRun === true) {
    //   socket.on('getPlayers', players => console.log(players))
    //   return
    //   let roomCode = localStorage.getItem('roomCode')
    //   let players = localStorage.getItem('players')

    //   socket.emit('gameStart', roomCode)
    //   socket.on('getPlayers', players => setPlayersInLobby(players))
    //   socket.on('gameStarted', gameStarted => setGameStarted(gameStarted))
    // }
  }

  useEffect(() => {
    if(showFooter && showNavContent) setShowFooter(false);
    
  },[showNavContent])
  
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Header 
        showNavContent={showNavContent}
        setShowNavContent={setShowNavContent}
      />


      <div className='App'>
        <Switch>
          <Route exact path='/'>
            <StartPage />
          </Route>
          <Route path='/create-game-form'>
            <CreateNewGameForm
              createNewGame={createNewGame}
              setTeamName={setTeamName}
              setCreatorName={setCreatorName}
            />
          </Route>
          <Route path='/create-game'>
            <CreateRoom
              joinedPlayers={joinedPlayers}
              gameCreator={gameCreator}
              roomCode={roomCode}
              startGame={startGame}
              removePlayer={removePlayer}
              setJoinedPlayers={setJoinedPlayers}
            />
          </Route>
          <Route path='/join-game'>
            <JoinGame
              joinGame={joinGame}
              setJoinRoomCode={setJoinRoomCode}
              setUserName={setUserName}
            />
          </Route>
          <Route path='/lobby'>
            <Lobby
              wrongCode={wrongCode}
              limitExceeded={limitExceeded}
              setLimitExceeded={setLimitExceeded}
              setWrongCode={setWrongCode}
              players={playersInLobby}
              gameCreator={gameCreator}
              gameStarted={gameStarted}
              socket={socket}
            />
          </Route>
          <Route path='/game'>
            <Game
              playersInGame={playersInLobby}
              socket={socket}
              roomCode={joinRoomCode}
              reset={reset}
              runMeAgain={runMeAgain}

            />
          </Route>
        </Switch>
      </div>

      <Footer 
        showFooter={showFooter}
        setShowFooter={setShowFooter}
      />
    </Router>
  )
}

export default App
